import { addIcons } from '@larva.io/webcomponents';
import { applyPolyfills, defineCustomElements } from '@larva.io/webcomponents/loader';
const icons: { icons: string[] } = require('@larva.io/webcomponents/dist/icons.json');

async function loadIcons(iconList: { icons: string[] }) {
    const iconPaths: { [key: string]: string; } = {};
    if (iconList && iconList.icons && Array.isArray(iconList.icons)) {
        for (const svg of iconList.icons) {
            const path = await import(/* webpackMode: "lazy-once" */`@larva.io/webcomponents/dist/collection/components/internal/icon/assets/svg/${svg}.svg`);
            if (path) {
                iconPaths[svg] = path.default;
            }
        }
    }
    addIcons(iconPaths);
}

loadIcons(icons).then(() => {
    return applyPolyfills()
  }).then(() => {
    return defineCustomElements();
  }).catch(err => {
      console.error(err);
  });

export const whenDefined = customElements.whenDefined;
export function isReady() {
    return customElements.whenDefined('lar-app');
};

export * from './proxies';
export * from '@larva.io/webcomponents';
